body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background: linear-gradient(to right, rgb(213, 124, 114), rgb(112, 215, 213));
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

@font-face {
	font-family: 'BRUX';
	src: url(/catfriends/static/media/BRUX-Regular.23f03bc2.eot);
	src: local('BRUX Regular'), local('BRUX-Regular'),
		url(/catfriends/static/media/BRUX-Regular.23f03bc2.eot?#iefix) format('embedded-opentype'),
		url(/catfriends/static/media/BRUX-Regular.a5ee85be.woff2) format('woff2'),
		url(/catfriends/static/media/BRUX-Regular.76e787a0.woff) format('woff'),
		url(/catfriends/static/media/BRUX-Regular.01b08c6a.ttf) format('truetype');
	font-weight: normal;
	font-style: normal;
}
h1 {
	font-family: 'BRUX';
	font-weight: 200;
}

